body{color:#6F6D6D;font-family:'Open Sans', sans-serif !important;font-size:16px}.section-header{height:688px;background-color:#F6F2F2;display:flex;align-items:center;padding-top:101px}.section-header h1{margin-bottom:36px}.section-header p{margin-bottom:57px;font-size:15px}h1,h2,h3,h4,h5,h6{color:#000}h1{font-size:60px;font-weight:800}h2{font-size:42px;font-weight:bold}h6{font-size:27px;font-weight:600}.icon{width:45px;height:45px;display:flex;align-items:center;justify-content:center}.btn{line-height:1;font-family:'Open Sans', sans-serif;font-size:13px;padding:20px 64px;font-weight:bold}.btn.btn-primary{background-color:#FF2D2D;border-color:#FF2D2D}.btn.btn-primary:hover{background-color:#c60000;border-color:#FF2D2D}.btn.btn-primary:focus{background-color:#c60000;border-color:#FF2D2D;box-shadow:0 0 0 0.2rem rgba(255,45,45,0.5)}.btn.btn-primary:active{background-color:#c60000 !important;border-color:#FF2D2D !important;box-shadow:0 0 0 0.2rem rgba(255,45,45,0.5) !important}.btn.btn-outline-primary{color:#FF2D2D;border-color:#FF2D2D}.btn.btn-outline-primary:hover{background-color:#FF2D2D;color:#ffffff}.btn.btn-outline-primary:focus{box-shadow:0 0 0 0.2rem rgba(255,45,45,0.5)}.btn.btn-outline-primary:active{background-color:#FF2D2D !important;color:#ffffff;box-shadow:0 0 0 0.2rem rgba(255,45,45,0.5) !important;border-color:#FF2D2D !important}.main-video{box-shadow:0px 0px 48px 13px rgba(0,0,0,0.49)}.section-two{padding-bottom:59px;padding-top:113px}.section-two .icon{margin-bottom:35px}.section-two h2{margin-bottom:50px}.section-two h6{margin-bottom:20px}.section-two .content-col{margin-bottom:48px}.section-two .text-two{margin-bottom:57px}hr{border:0;height:3px;background-color:#ECECEC}
